<script>
	import { userApi } from '../../api/index.js'
	import { showSuccess, showError, showConfirm } from '../../utils/index.js'
	
	export default {
		data() {
			return {
				userInfo: {},
				showModal: false,
				saving: false,
				editForm: {
					nickname: '',
					avatar: '',
					bio: ''
				}
			}
		},
		onLoad() {
			this.loadUserInfo()
		},
		onShow() {
			this.loadUserInfo()
		},
		methods: {
			// Âä†ËΩΩÁî®Êà∑‰ø°ÊÅØ
			loadUserInfo() {
				this.userInfo = uni.getStorageSync('userInfo') || {}
			},
			
			// ÊòæÁ§∫ÁºñËæëÂºπÁ™ó
			showEditModal() {
				this.editForm = {
					nickname: this.userInfo.nickname || '',
					avatar: this.userInfo.avatar || '',
					bio: this.userInfo.bio || ''
				}
				this.showModal = true
			},
			
			// ÈöêËóèÁºñËæëÂºπÁ™ó
			hideModal() {
				this.showModal = false
			},
			
			// ‰øùÂ≠òËµÑÊñô
			async saveProfile() {
				this.saving = true
				try {
					const response = await userApi.updateProfile(this.editForm)
					if (response.success) {
						// Êõ¥Êñ∞Êú¨Âú∞Â≠òÂÇ®ÁöÑÁî®Êà∑‰ø°ÊÅØ
						const updatedUserInfo = { ...this.userInfo, ...this.editForm }
						uni.setStorageSync('userInfo', updatedUserInfo)
						this.userInfo = updatedUserInfo
						
						showSuccess('ËµÑÊñôÊõ¥Êñ∞ÊàêÂäü')
						this.hideModal()
					}
				} catch (error) {
					showError('Êõ¥Êñ∞Â§±Ë¥•')
				} finally {
					this.saving = false
				}
			},
			
			// Ë∑≥ËΩ¨È°µÈù¢
			goToPage(url) {
				if (url.includes('/pages/home/home') || url.includes('/pages/team/team') || url.includes('/pages/activity/activity') || url.includes('/pages/message/message')) {
					uni.switchTab({ url })
				} else {
					uni.navigateTo({ url })
				}
			},
			
			// ÊòæÁ§∫ÂÖ≥‰∫é‰ø°ÊÅØ
			showAbout() {
				uni.showModal({
					title: 'ÂÖ≥‰∫éÁÆÄÂ∫êÂõ¢Èòü',
					content: 'ÁÆÄÂ∫êÂõ¢Èòü v1.0.0\n\n‰∏Ä‰∏™Áé∞‰ª£ÂåñÁöÑÂõ¢ÈòüÂçè‰ΩúÂíåÊ¥ªÂä®ÁÆ°ÁêÜÂπ≥Âè∞„ÄÇ\n\nÈ´òÊïàÂçè‰ΩúÔºåÂÖ±ÂàõÊú™Êù•ÔºÅ',
					showCancel: false
				})
			},
			
			// ÈÄÄÂá∫ÁôªÂΩï
			async logout() {
				const confirmed = await showConfirm('Á°ÆÂÆöË¶ÅÈÄÄÂá∫ÁôªÂΩïÂêóÔºü')
				if (!confirmed) return
				
				// Ê∏ÖÈô§Êú¨Âú∞Â≠òÂÇ®
				uni.removeStorageSync('token')
				uni.removeStorageSync('userInfo')
				
				showSuccess('Â∑≤ÈÄÄÂá∫ÁôªÂΩï')
				
				// Ë∑≥ËΩ¨Âà∞ÁôªÂΩïÈ°µ
				setTimeout(() => {
					uni.reLaunch({
						url: '/pages/login/login'
					})
				}, 1000)
			}
		}
	}
</script>

